import {useState,useEffect} from 'react';
import axios from 'axios';
import './CrudStyle.css';

const API_URL = "https://60ca0dad772a7600172052ea.mockapi.io/users";

function CrudPost(){
    const [state,setState] = useState({
        posts:[],
        id:'',
        userId:'',
        title:'',
        body:'',
    });

    useEffect(() =>{
        getPost();
    }, [])
    
    const getPost = async()=>{
        try{
        const {data} = await axios.get(API_URL);
        setState({...state ,posts:data})
        }
        catch(err){
            console.log(err)
        }

    }
    const deletePost = async(postId)=>{
        try{
            await axios.delete(`${API_URL}/${postId}`);
            const delPost = state.posts.filter((post)=>{
                return post.id !== postId;
            })
            setState({...state, posts:delPost})
            
        }
        catch(err){
            console.log(err)
        }
    }

    const changeText = ({target:{name,value}}) =>{
        setState({...state,[name]:value})
    }

    const formSubmit = (e) => {
        e.preventDefault();
        if(state.id){
            editPost();
        }
        else{
            addPost();
        }
    }

    const addPost = async() =>{
        try{
        const post = await axios.post(API_URL,{
            userId:state.userId,
            title:state.title,
            body:state.body,
        });
        const receive = [...state.posts];
        receive.push(post.data);
        setState({...state,posts:receive,id:"",userId:"",title:"",body:""})
        // console.log(post.data)
    }
    catch(err){
        console.log(err)
    }
    }

    // const selectPost = (e) =>{
    //     console.log(!e.id)
    //     if(!state.id){
    //         addPost();
    //     }
    //     else{
    //         editPost();
    //     }
    // }

    const selectPost = ({id,userId,title,body}) =>{
        setState({...state, id:id, userId:userId, title:title, body:body})
    }

    const editPost = async() => {
        try{
            const {data}=await axios.put(`${API_URL}/${state.id}`, {
                userId:state.userId,
                title:state.title,
                body:state.body
            })
            console.log(data);
            const index = state.posts.findIndex(post => 
                post.id === state.id
            )
            state.posts[index] = data;
            setState({...state,id:"",userId:"",title:"",body:""})
        }
        catch(err){
            console.log(err)
        }
    }

    return(
        <>
        <div class="container">
        <form onSubmit={formSubmit}>
            {/* <label>Id</label>
            <input type="text" name="id" value={state.id} onChange={changeText}/> */}
            <div class="form-input">
            <fieldset>
            <input placeholder="User Id" type="text" name="userId" value={state.userId} onChange={changeText}/>
            </fieldset>
            <fieldset>
            <input placeholder="Title" type="text" name="title" value={state.title} onChange={changeText}/>
            </fieldset>
            <fieldset>
            <input placeholder="Body" type="text" name="body" value={state.body} onChange={changeText}/>
            </fieldset>
            <button class="btn btn-success">Submit</button>
            </div>
        </form>
        <table class="table table-hover">
            <thead class="">
                <tr>
                    <th>Id</th>
                    <th>User Id</th>
                    <th>Title</th>
                    <th>Body</th>
                    <th>Action</th>
                </tr>
            </thead>
        {state.posts.map((post,index)=>{
            return(
            <tbody key={index}>
                <tr>
                    <td>{post.id}</td>
                    <td>{post.userId}</td>
                    <td>{post.title}</td>
                    <td><img src={post.body}></img></td>
                    <td>
                    <button class="btn btn-danger" onClick={()=>deletePost(post.id)}>Delete</button>
                    <button class="btn btn-success" onClick={()=>selectPost(post)}>Edit</button>
                    </td>
                </tr>
            </tbody>
            )

        })}
        </table>
        </div>
        </>
        
    )
}

export default CrudPost;