import { CounterContext } from "./Context";
import Test2 from "./Test2";

function Test1() {
    return(
        <CounterContext.Consumer>
            {(Context)=>{return(
        <>
        <p>Test1</p>
        <p>count - {Context.count}</p>
        <Test2/>
        </>
        )}}
        </CounterContext.Consumer>
    )
    
}

export default Test1;